let gallery = document.querySelector('.gallery');


const createHTMLElementsFromString = (htmlString) => {
  let div = document.createElement('div');
  div.innerHTML = htmlString.trim();
  return div.firstChild;
}

const generateImageElements = (imageArray) => {
  let allImagesFrag = new DocumentFragment();
  imageArray.forEach((image) => {
    const { original, preview, description } = image;
    allImagesFrag.appendChild(createHTMLElementsFromString(`
        <li class="gallery-item">
              <a class="gallery-link" href="${original}">
                <img
                  class="gallery-image"
                  src="${preview}"
                  alt="${description}"
                />
              </a>
        </li>`
    ));
  });

  return allImagesFrag;
}

gallery.appendChild(generateImageElements(images));

new SimpleLightBox('.gallery .gallery-item a', {
  captionDelay: 250,
  captionsData: 'alt',
  showCounter: false,
});